import { useCallback, useMemo, useState } from 'react'
import { Place } from '../types'

export const ALL_REGION = '全部區域'
export const ALL_DISTRICT = '全部地區'

export function usePlaceFilter(
  placeList: Array<Place>,
  setSelectedPlace: (place: Place | null) => void,
  onChangePlaceFilter: () => void
): [
  string,
  string,
  (region: string) => void,
  (district: string) => void,
  (keyword: string) => void,
  Array<Place>,
  boolean,
] {
  const [selectedRegion, setSelectedRegion] = useState<string>(ALL_REGION)
  const [selectedDistrict, setSelectedDistrict] = useState<string>(ALL_DISTRICT)
  const [searchKeyword, setSearchKeyword] = useState<string>('')
  const [isDisableSelectDistrict, setIsDisableSelectDistrict] = useState<boolean>(true)

  const handleSelectRegion = useCallback(
    (region: string) => {
      setSelectedRegion(region)
      setSelectedDistrict(ALL_DISTRICT)

      setIsDisableSelectDistrict(region === ALL_REGION)
      onChangePlaceFilter()
    },
    [onChangePlaceFilter]
  )

  const handleSelectDistrict = useCallback(
    (district: string) => {
      setSelectedDistrict(district)
      onChangePlaceFilter()
    },
    [onChangePlaceFilter]
  )

  const handleSelectKeyword = useCallback(
    (keyword: string) => {
      setSearchKeyword(keyword)
      onChangePlaceFilter()
    },
    [onChangePlaceFilter]
  )

  const filteredPlaceList = useMemo(() => {
    let filtered = placeList

    // First filter by region and district
    filtered = filtered.filter((place) => {
      if (selectedRegion === ALL_REGION) {
        return true
      }
      if (selectedDistrict === ALL_DISTRICT) {
        return place.region === selectedRegion
      }
      return place.district === selectedDistrict
    })

    // Then filter by keyword (like SQL %keyword%)
    if (searchKeyword.trim()) {
      filtered = filtered.filter((place) => place.name.toLowerCase().includes(searchKeyword.toLowerCase()))
    }

    if (filtered.length > 0) {
      setSelectedPlace(filtered[0])
    } else {
      setSelectedPlace(null)
    }
    return filtered
  }, [placeList, selectedDistrict, selectedRegion, searchKeyword, setSelectedPlace])

  return [
    selectedRegion,
    selectedDistrict,
    handleSelectRegion,
    handleSelectDistrict,
    handleSelectKeyword,
    filteredPlaceList,
    isDisableSelectDistrict,
  ]
}
